.hamburger-icon {
  position: relative;
  display: inline-block;
  background: transparent;
  border: none;
  size: 44px;
  font-size: 1.5rem;
  padding: 0;
  cursor: pointer;
  color: currentColor;
}

@media (min-width: 768px) {
  .hamburger-icon {
    display: none;
  }
}

.hamburger-icon span {
  position: absolute;
  line-height: 0;
  right: 3em;
  top: 50%;
  bottom: 0;
  font-size: 1rem;
}

@media (max-width: 575px) {
  .hamburger-icon span {
    font-size: 0;
    width: 1px;
    height: 1px;
    overflow: hidden;
  }
}

.hamburger-icon::before, .hamburger-icon::after {
  content: "";
  width: 1em;
}

.hamburger-icon::before, .hamburger-icon::after,
.hamburger-icon i {
  position: absolute;
  top: 50%;
  display: block;
  height: 2px;
  background-color: currentColor;
  transition: transform 0.4s ease-in-out-circ;
  will-change: transform;
  left: 50%;
}

.hamburger-icon::before {
  transform: translate(-50%, -0.25em);
}

.hamburger-icon::after {
  transform: translate(-50%, 0.25em);
}

.hamburger-icon i {
  width: 0.7em;
  transform: translateX(-0.5em);
}

.hamburger-icon:hover i {
  transform: translateX(-0.2em);
}

.hamburger-icon:active::before,
.-menuopen .hamburger-icon::before {
  transform: translateX(-50%) rotate(-45deg);
}

.hamburger-icon:active::after,
.-menuopen .hamburger-icon::after {
  transform: translateX(-50%) rotate(45deg);
}

.hamburger-icon:active i,
.-menuopen .hamburger-icon i {
  transform: translateX(5em);
}

.hamburger-icon:hover {
  outline: none;
}

.hamburger-icon:not(:hover):focus {
  box-shadow: 0 0 2px currentColor;
}

.page-header {
  position: fixed 0 0 x;
  padding: var(--safe-area-inset-top) var(--safe-area-inset-right) 0 var(--safe-area-inset-left);
  z-index: 1050;
  transition: transform 0.6s ease;
  font-weight: 300;
  font-size: 18px;
  display: grid;
  grid-template: "logo hamburger" auto "mobile mobile" 1fr / auto auto;
}

@media (min-width: 768px) {
  .page-header {
    grid-template: ". logo navigation ." auto / 0 auto 1fr 0;
  }
  body > .page-header {
    grid-template-columns: 1fr 270px minmax(auto, 1360px) 1fr;
  }
  .page-header > .mainnav {
    grid-area: navigation;
    justify-self: flex-end;
    align-self: stretch;
    display: flex;
    justify-content: center;
  }
}

.page-header + * {
  padding-top: 69px;
}

.page-header::before {
  content: "";
  position: absolute 5px 0 0;
  box-shadow: 0 0 5px #444;
  z-index: -3;
  opacity: 1;
}

.page-header::after {
  content: "";
  position: absolute 0;
  background-color: #fff;
  transition: opacity 0.6s ease;
  z-index: -2;
}

.logo-element {
  display: block;
  grid-area: logo;
  justify-self: start;
  margin: 15px 15px 15px 30px;
}

.logo-element,
.logo-element .icon-logo {
  width: 100%;
  height: auto;
  max-size: size("Logo.svg");
}

.hamburger-icon {
  grid-area: hamburger;
  justify-self: end;
  align-self: center;
}

.mainnav {
  grid-area: mobile;
  align-self: center;
  line-height: 1.1;
}

@media (max-width: 767px) {
