    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fff;
    left: 50%;
    transform: translate(-50%, -6px);
    z-index: 10;
  }
  .mainnav__link--level2 {
    padding: 5px 20px;
  }
}

@media (max-width: 767px) {
  .-menuopen {
    overflow: hidden;
  }
  .-menuopen body::before {
    opacity: 1;
  }
  .-menuopen .page-header::before {
    opacity: 0;
  }
  .-menuopen .page-header .hamburger-icon,
  .-menuopen .page-header .logo-element {
    z-index: 1001;
