  .-menuopen .mainnav {
    z-index: 1000;
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
  }
  .-menuopen .mainnav__list--level1 {
    transform: translateY(0);
  }
  body::before {
    content: "";
    position: fixed 0;
    background-image: linear-gradient(white 100px, rgba(255, 255, 255, 0.9));
    transition: opacity 0.4s ease;
    opacity: 0;
    pointer-events: none;
