    transition-delay: 0.3s;
  }
  .mainnav__element:nth-child(4) {
    transition-delay: 0.4s;
  }
  .mainnav__element:nth-child(5) {
    transition-delay: 0.5s;
  }
  .mainnav__element:nth-child(6) {
